// Avoid `console` errors in browsers that lack a console.
'use strict';
// Tab Hover Style
 jQuery(document).ready(function($) {
       
  jQuery("#owl-demo").hide();
  
  jQuery("#owl-demo").imagesLoaded(function () {
    jQuery("#owl-demo").owlCarousel({
        loop: true,
        center: true,
        items: 1,
        margin:10,
        nav: true,
        lazyLoad: true,
        video: true,
        dots: true,
        autoHeight: true,
        //autoWidth:true,
        responsive:{
            0:{
                items:1
            },
            600:{
                items:1
            },
            1000:{
                items:1
            }
        }
    });
    jQuery("#owl-demo").show();
  })
  
  jQuery("#camex-feature-slider").hide();
  jQuery("#camex-feature-slider").imagesLoaded( function () {
    $('#camex-feature-slider').owlCarousel({
      loop: true,
      margin: 10,
      nav: true,
      dots: true,
      responsive: {
          0:{
              items:1
          },
          768:{
              items:3
          },
          1160:{
              items:5
          }
      }
    });
  jQuery("#camex-feature-slider").show();
 });
  });
 
  